@import url(https://fonts.googleapis.com/css?family=Monoton&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1f181b;
  text-align: center;
  height: 100vh;
}

.Cell {
  height: 100px;
  width: 100px;
  background-color: #161113;
  -webkit-transition: background-color 0.25s;
  transition: background-color 0.25s;
  border-radius: 3px;
}

.Cell-lit {
  background-color: #fcebf5;
}

.Board {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Board-table {
  margin: 0 auto;
  box-shadow: 0px 0px 50px #fff;
}

.Board-title {
  text-align: center;
  font-size: 2.2em;
  color: #fff;
  padding: 0 20px 0 20px;
  font-family: Monoton;
}

.Board-red {
  -webkit-animation: neon1 1.5s ease-in-out infinite alternate;
          animation: neon1 1.5s ease-in-out infinite alternate;
}
.Board-blue {
  -webkit-animation: neon2 1.3s ease-in-out infinite alternate;
          animation: neon2 1.3s ease-in-out infinite alternate;
}

@-webkit-keyframes neon1 {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #ff1177, 0 0 70px #ff1177, 0 0 80px #ff1177, 0 0 100px #ff1177,
      0 0 150px #ff1177;
  }
  to {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #ff1177, 0 0 35px #ff1177, 0 0 40px #ff1177, 0 0 50px #ff1177,
      0 0 75px #ff1177;
  }
}

@keyframes neon1 {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #ff1177, 0 0 70px #ff1177, 0 0 80px #ff1177, 0 0 100px #ff1177,
      0 0 150px #ff1177;
  }
  to {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #ff1177, 0 0 35px #ff1177, 0 0 40px #ff1177, 0 0 50px #ff1177,
      0 0 75px #ff1177;
  }
}
@-webkit-keyframes neon2 {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px hsl(199, 100%, 53%), 0 0 70px hsl(199, 100%, 53%),
      0 0 80px hsl(199, 100%, 53%), 0 0 100px hsl(199, 100%, 53%), 0 0 150px hsl(199, 100%, 53%);
  }
  to {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px hsl(199, 100%, 53%), 0 0 35px hsl(199, 100%, 53%),
      0 0 40px hsl(199, 100%, 53%), 0 0 50px hsl(199, 100%, 53%), 0 0 75px hsl(199, 100%, 53%);
  }
}
@keyframes neon2 {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px hsl(199, 100%, 53%), 0 0 70px hsl(199, 100%, 53%),
      0 0 80px hsl(199, 100%, 53%), 0 0 100px hsl(199, 100%, 53%), 0 0 150px hsl(199, 100%, 53%);
  }
  to {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px hsl(199, 100%, 53%), 0 0 35px hsl(199, 100%, 53%),
      0 0 40px hsl(199, 100%, 53%), 0 0 50px hsl(199, 100%, 53%), 0 0 75px hsl(199, 100%, 53%);
  }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

